<template>
<Row class="vm-table vm-panel">
    <div class="panel-heading">
      {{ title }}
    </div>
    <div class="panel-body">
      <Row type="flex" justify="space-between" class="control">
        <div class="search-bar">
        <Row type="flex" :gutter="16">
          <Col>
              <Input placeholder="" v-model="searchModel.name" style="width: 200px"></Input>
          </Col>
          <Col>
              <Select v-model="searchModel.frozen" placeholder="">
                  <Option v-for="item in playerFreezeObject" :value="item.value" :key="item.key">
                      {{ item.key }}
                  </Option>
              </Select>
          </Col>
          <Col>
              <Select v-model="searchModel.playerType" placeholder="">
                  <Option v-for="item in playerTypeObject" :value="item.value" :key="item.key">
                      {{ item.key }}
                  </Option>
              </Select>
          </Col>          
          <Col>
              <Button type="ghost" @click="searchEvent"><i class="fa fa-search"></i></Button>
          </Col>
        </Row>
        </div>
      </Row>
      <div class="edit" v-if="toolbar">
          <Button  :disabled="adoptDisabled" @click="modalAdopt = true"><i class="fa fa-plus"></i> </Button>
      </div>
      <Table ref="selection" :loading="tableLoading" :stripe="showStripe" :size="tableSize" :columns="showColumns" :data="dataShow" @on-selection-change="selectChange"></Table>
      <Row type="flex" justify="space-between" class="footer">
        <div class="page">
          <Page :total="total" :current="currentPage" :page-size="showNum" @on-change="pageChange" show-total show-elevator></Page>
        </div>
      </Row>
    </div>
    <Modal
        v-model="modalAdopt"
        title=""
        ok-text=""
        cancel-text=""
        @on-ok="confirmOkEvent">
        {{modalConfirmText}}
    </Modal>
  </Row>
</template>
<script>
import moment from "moment";
import { HTTP_URL_API } from "../../data/api";
import { HttpGet,HttpPost,SerializeForm, FormatMoney,MakeSign } from "../../data/utils";
export default {
  data() {
    return {
      title: "",
      keyword: "",
      toolbar: false,
      modalConfirm: false,
      adoptDisabled: true,
      tableLoading: true,
      modalConfirmText: "?",
      playerFreezeObject: [
        { key: "", value: -1 },
        { key: "", value: 0 },
        { key: "", value: 1 }
      ],
      playerTypeObject: [
        { key: "", value: -2 },
        { key: "", value: -1 },
        { key: "", value: 0 },
        { key: "", value: 1 }
      ],
      modalAdopt: false,
      total: 0,
      showNum: 20,
      currentPage: 1,
      showStripe: true,
      tableSize: "small",
      dataShow: [],
      goldNumber: 100, //
      goldType: 1, //(-1,)
      showColumns: [
        {
          type: "index",
          width: 60,
          align: "center"
        },
        {
          title: "",
          key: "name",
          ellipsis: true
        },
        {
          title: "",
          key: "account",
          ellipsis: true
        },
        {
          title: "",
          key: "gold",
          ellipsis: true
        },
        {
          title: "",
          key: "goldingot",
          ellipsis: true
        },
        {
          title: "()",
          key: "totalMoney",
          ellipsis: true
        },
        {
          title: "",
          key: "updatename",
          ellipsis: true,
          render: (h, params) => {
            return params.row.updatename > 0 ? "" : "";
          }
        },
        {
          title: "",
          key: "playerType",
          ellipsis: true,
          render: (h, params) => {
            switch (params.row.playerType) {
              case 1:
                return "";
              case 0:
                return "";
              default:
                return "";
            }
          }
        },
        {
          title: "",
          key: "frozen",
          ellipsis: true,
          render: (h, params) => {
            switch (params.row.frozen) {
              case 1:
                return "";
              default:
                return "";
            }
          }
        },
        {
          title: "",
          key: "lastLoginTime",
          ellipsis: true,
          render: (h, params) => {
            return this.Moment(params.row.lastLoginTime, "YYYY-MM-DD HH:mm:ss");
          }
        },
        {
          title: "",
          key: "registerTime",
          ellipsis: true,
          render: (h, params) => {
            return this.Moment(params.row.registerTime, "YYYY-MM-DD HH:mm:ss");
          }
        }
      ],
      searchModel: {
        name: "",
        playerType: null,
        frozen: null,
        p: 1,
        method: "online"
      }
    };
  },
  methods: {
    searchEvent: function() {
      this.searchModel.p = 1;
      this.initTableData();
    },
    confirmOkEvent: function() {
      this.$Notice.success({
        title: ""
      });
    },
    pageChange: function(page) {
      this.searchModel.p = page;
      this.currentPage = page;
      this.initTableData();
    },
    selectChange: function(data) {
      this.dataSelect = data;
    },
    initTableData: function() {
      this.tableLoading = true;
      HttpGet(HTTP_URL_API.GET_PLAYER_LIST, this.searchModel)
        .then(result => {
          if (result && result.data.result.data.list.length > 0) {
            this.dataShow = result.data.result.data.list;
            this.total = result.data.result.data.count;
          } else {
            this.dataShow = [];
            this.total = 0;
          }
        })
        .then(() => {
          setTimeout(() => {
            this.tableLoading = false;
          }, 800);
        });
    },
    Moment(date, format) {
      return moment(date).format(format);
    }
  },
  mounted: function() {
    this.initTableData();
  }
};
</script>


