<template>
    <div>
        <Row>
            <Alert type="warning" show-icon>,</Alert>
            <Form :model="formModel" :label-width="50">
                <FormItem label="" prop="goldpool">
                    <InputNumber :max="99999999999" :min="0" v-model="formModel.goldpool" placeholder="" style="width:300px"></InputNumber>
                </FormItem>
            </Form>
        </Row>
        <div class="edit">
            <Button @click="modalShow = true" ><i class="fa fa-plus"></i></Button>
        </div>
        <Table :stripe="showStripe" :size="tableSize" :columns="columns" :data="tableConfigs"></Table>
        <Modal
            v-model="modalShow"
            :title="modalTitle"
            :mask-closable="false"
            ok-text=""
            cancel-text=""
            v-on:on-ok="modalSaveEvent">
            <Form :label-width="120" ref="formModel" :model="formModel" :rules="ruleValidate">
                <FormItem label="" prop="minold">
                    <InputNumber v-model="formModel.configs.minGold"  placeholder="" style="width:200px"></InputNumber>
                </FormItem>
                <FormItem label="" prop="maxGold">
                    <InputNumber v-model="formModel.configs.maxGold"  placeholder="" style="width:200px"></InputNumber>
                </FormItem>
                <FormItem label="" prop="rate">
                    <InputNumber v-model="formModel.configs.rate" :min="-1" :max="100" placeholder="" style="width:200px"></InputNumber>
                </FormItem>                
            </Form>            
        </Modal>          
    </div>
</template>
<script>
export default {
  props: {
    columns: Array,
    tableConfigs: Array,
    ruleValidate: Object,
    formModel: Object,
    modalTitle: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      showStripe: true,
      tableSize: "small",
      modalShow:false
    };
  },
  methods: {
    modalSaveEvent: function() {
      this.$emit("modalSaveEvent", this.formModel);
    }
  }
};
</script>


